import React, { useEffect, useState } from "react";
import { useLoginMutation, useUsersQuery } from "../generated/graphql";
import "../css/main.css";
import { getAccessToken, setAccessToken } from "../accessToken";
import { checkIfAuth } from "../Auth";
import jwtDecode from "jwt-decode";
import EditAccount from "./EditAccount";
import Register from "./Register";
interface Props {}

export const Home: React.FC<Props> = () => {
    const [first_name, setFirstName] = useState("");
    const [last_name, setLastName] = useState("");
    const [password, setPassword] = useState("");
    const { data, loading } = useUsersQuery();
    const [registerState, setRegister] = useState(false);
    const [age, setAge] = useState("");
    const [phone_number, setPhoneNumber] = useState("");

    const [LOGIN] = useLoginMutation();

    useEffect(() => {
        if (first_name) {
            try {
                document.getElementById("fn-label")!.style.transform =
                    "translateY(0)";
            } catch {}
        }
        if (last_name) {
            try {
                document.getElementById("ln-label")!.style.transform =
                    "translateY(0)";
            } catch {}
        }
        if (age) {
            try {
                document.getElementById("age-label")!.style.transform =
                    "translateY(0)";
            } catch {}
        }
        if (phone_number) {
            try {
                document.getElementById("pn-label")!.style.transform =
                    "translateY(0)";
            } catch {}
        }
    });

    if (loading) {
        return <div>loading...</div>;
    } else if (!data) {
        return <div></div>;
    }

    const login = async () => {
        const response = await LOGIN({
            variables: {
                first_name,
                last_name,
                password,
            },
        });

        if (response && response.data) {
            setAccessToken(response.data.login.accessToken || "");
            localStorage.setItem("urd", response.data.login.refreshToken || "");
            window.location.reload();
        }
    };

    return (
        <div className="pageWrapper">
            <div className="verticle-align">
                <div className="horizontal-align">
                    <div className="contentWrapper">
                        <div className="left">
                            <div className="text">
                                <h1>Users :</h1>

                                <ul>
                                    {data.users.map((val, i) => {
                                        const token = getAccessToken();
                                        let userId;
                                        if (!!token) {
                                            let tmp = jwtDecode(token) as any;
                                            userId = tmp.userId;
                                        }

                                        if (
                                            checkIfAuth() &&
                                            data.users[i].id === userId
                                        ) {
                                            if (
                                                first_name === "" &&
                                                last_name === ""
                                            ) {
                                                setFirstName(
                                                    data.users[i].first_name
                                                );
                                                setLastName(
                                                    data.users[i].last_name
                                                );

                                                setAge(data.users[i].age);
                                                setPhoneNumber(
                                                    data.users[i].phone_number
                                                );
                                            }

                                            return (
                                                <li key={i}>
                                                    * {val.first_name},{" "}
                                                    {val.last_name}
                                                </li>
                                            );
                                        } else {
                                            return (
                                                <li key={i}>
                                                    {val.first_name},{" "}
                                                    {val.last_name}
                                                </li>
                                            );
                                        }
                                    })}
                                </ul>
                            </div>
                        </div>
                        <div className="right">
                            <div className="login-box">
                                {checkIfAuth() ? (
                                    <EditAccount
                                        props={{
                                            first_name,
                                            setFirstName,
                                            last_name,
                                            setLastName,
                                            password,
                                            setPassword,
                                            age,
                                            setAge,
                                            phone_number,
                                            setPhoneNumber,
                                        }}
                                    />
                                ) : (
                                    <>
                                        {!registerState ? (
                                            <>
                                                <h1>Log In</h1>
                                                <h5
                                                    style={{
                                                        textAlign: "center",
                                                        color: "blue",
                                                    }}
                                                >
                                                    <span
                                                        onClick={() => {
                                                            setRegister(true);
                                                        }}
                                                    >
                                                        Or Register
                                                    </span>
                                                </h5>
                                                <div className="center">
                                                    <div className="container">
                                                        <UserDataInputs
                                                            props={{
                                                                first_name,
                                                                setFirstName,
                                                                last_name,
                                                                setLastName,
                                                                password,
                                                                setPassword,
                                                            }}
                                                        />
                                                    </div>
                                                </div>
                                                <div className="center">
                                                    <button
                                                        className="login-btn"
                                                        onClick={() => login()}
                                                    >
                                                        LOG IN
                                                    </button>
                                                </div>
                                            </>
                                        ) : (
                                            <>
                                                <Register
                                                    props={{
                                                        first_name,
                                                        setFirstName,
                                                        last_name,
                                                        setLastName,
                                                        password,
                                                        setPassword,
                                                        age,
                                                        setAge,
                                                        phone_number,
                                                        setPhoneNumber,
                                                    }}
                                                />
                                            </>
                                        )}
                                    </>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export const UserDataInputs = ({ props }: any) => {
    let {
        first_name,
        setFirstName,
        last_name,
        setLastName,
        password,
        setPassword,
    } = props;

    return (
        <>
            <div
                className="input-box"
                style={{
                    marginLeft: "5%",
                    width: "40%",
                    display: "inline-block",
                }}
            >
                <input
                    id="fn-input"
                    onClick={() => {
                        try {
                            document.getElementById(
                                "fn-label"
                            )!.style.transform = "translateY(0)";
                        } catch {}
                    }}
                    onFocus={() => {
                        try {
                            document.getElementById(
                                "fn-label"
                            )!.style.transform = "translateY(0)";
                        } catch {}
                    }}
                    onBlur={() => {
                        if (!first_name) {
                            document.getElementById(
                                "fn-label"
                            )!.style.transform = "";
                        }
                    }}
                    value={first_name}
                    onChange={e => setFirstName(e.target.value)}
                />
                <label id="fn-label">First Name</label>
            </div>

            <div
                className="input-box"
                style={{
                    marginLeft: "5%",
                    width: "40%",
                    display: "inline-block",
                }}
            >
                <input
                    id="ln-input"
                    onClick={() => {
                        try {
                            document.getElementById(
                                "ln-label"
                            )!.style.transform = "translateY(0)";
                        } catch {}
                    }}
                    onFocus={() => {
                        try {
                            document.getElementById(
                                "ln-label"
                            )!.style.transform = "translateY(0)";
                        } catch {}
                    }}
                    onBlur={() => {
                        if (!last_name) {
                            document.getElementById(
                                "ln-label"
                            )!.style.transform = "";
                        }
                    }}
                    value={last_name}
                    onChange={e => setLastName(e.target.value)}
                />
                <label id="ln-label">Last Name</label>
            </div>

            <div
                className="input-box"
                style={{
                    width: "85%",
                    marginLeft: "5%",
                }}
            >
                <input
                    id="p-input"
                    onClick={() => {
                        try {
                            document.getElementById(
                                "p-label"
                            )!.style.transform = "translateY(0)";
                        } catch {}
                    }}
                    onFocus={() => {
                        try {
                            document.getElementById(
                                "p-label"
                            )!.style.transform = "translateY(0)";
                        } catch {}
                    }}
                    onBlur={() => {
                        if (!password) {
                            document.getElementById(
                                "p-label"
                            )!.style.transform = "";
                        }
                    }}
                    value={password}
                    onChange={e => setPassword(e.target.value)}
                    type="password"
                />
                <label id="p-label">Password</label>
            </div>
        </>
    );
};
